module.exports = "\n\n@import url('https://fonts.googleapis.com/css?family=Roboto:300');\n/* .container{\n    border: 1px solid black;\n    padding: 10px;\n}\n\n.login-btn{\n    background-color: #6A93F2;\n    border-radius: 5px;\n    color: white;\n    height: 40px;\n    width: 70px;\n} */\n* {\n  box-sizing: border-box;\n}\nbody {\n    font-family: 'Roboto', sans-serif;\n    font-weight: 300;\n    background: #181818;\n    color: #fff;\n    overflow: hidden;\n  }\nh1, h2, h3, h4, h5, h6 {\n  font-weight: 300;\n}\n#wrapper {\n    margin-top: 10px;\n    display: flex;\n    flex-direction: row;\n  }\n#left {\n    display: flex;\n    flex-direction: column;\n    flex: 1;\n    align-items: center;\n    justify-content: center;\n    height: 100vh;\n}\n#right {\n    flex: 1;\n}\n/* Sign In */\n#signin {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    width: 80%;\n    padding-bottom: 1rem;\n}\n#signin form {\n    width: 80%;\n    padding-bottom: 3rem;\n}\n#signin .logo {\n    margin-bottom: 8vh;\n}\n#signin .logo img {\n    width: 300px;\n}\n#signin label {\n    font-size: 0.9rem;\n    line-height: 1rem;\n    font-weight: 500;\n}\n#signin .text-input {\n    /* margin-bottom: 1.3rem; */\n    width: 100%;\n    border-radius: 2px;\n    background: #181818;\n    border: 1px solid #555;\n    color: #ccc;\n    padding: 0.5rem 1rem;\n    line-height: 1.3rem;\n}\n#signin .primary-btn {\n    width: 100%;\n    margin-top: 1.3rem;\n}\n#signin .secondary-btn,\n  .or,\n  .links {\n    width: 60%;\n}\n#signin .links a {\n    display: block;\n    color: black;\n    text-decoration: none;\n    margin-bottom: 1rem;\n    text-align: center;\n    font-size: 0.9rem;\n}\n#signin .or {\n    display: flex;\n    flex-direction: row;\n    margin-bottom: 1.2rem;\n    align-items: center;\n}\n#signin .or .bar {\n    flex: auto;\n    border: none;\n    height: 1px;\n    background: #aaa;\n}\n#signin .or span {\n    color: black;\n    padding: 0 0.8rem;\n}\n/* Showcase */\n#showcase {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    background: url('https://i.imgur.com/1MEaKF4.jpg') no-repeat center center / cover;\n    height: 100vh;\n    text-align: center;\n}\n#showcase .showcase-text {\n    font-size: 3rem;\n    width: 100%;\n    color: white;\n    margin-bottom: 1.5rem;\n}\n#showcase .secondary-btn {\n    width: 60%;\n    margin: auto;\n}\n/* Footer */\n#main-footer {\n    color: #ccc;\n    text-align: center;\n    font-size: 0.8rem;\n    max-width: 80%;\n    padding-top: 5rem;\n}\n#main-footer a {\n    color: #f96816;\n    text-decoration: underline;\n}\n/* Button */\n.primary-btn {\n    padding: 0.7rem 1rem;\n    height: 2.7rem;\n    display: block;\n    border: 0;\n    border-radius: 2px;\n    font-weight: 500;\n    background: #f96816;\n    color: #fff;\n    text-decoration: none;\n    cursor: pointer;\n    text-align: center;\n    transition: all 0.5s;\n}\n.primary-btn:hover {\n    background-color: #ff7b39;\n}\n.secondary-btn {\n    padding: 0.7rem 1rem;\n    height: 2.7rem;\n    display: block;\n    border: 1px solid #f4f4f4;\n    border-radius: 2px;\n    font-weight: 500;\n    background: none;\n    color: rgb(67, 67, 67);\n    text-decoration: none;\n    cursor: pointer;\n    text-align: center;\n    transition: all 0.5s;\n}\n.secondary-btn:hover {\n    border-color: #ff7b39;\n    color: #ff7b39;\n}\n/* Media Queries */\n@media (min-width: 1200px) {\n    #left {\n      flex: 4;\n    }\n  \n    #right {\n      flex: 6;\n    }\n}\n@media (max-width: 768px) {\n    body {\n      overflow: auto;\n    }\n  \n    #right {\n      display: none;\n    }\n  \n    #left {\n      justify-content: start;\n      margin-top: 4vh;\n    }\n  \n    #signin .logo {\n      margin-bottom: 2vh;\n    }\n  \n    #signin .text-input {\n      margin-bottom: 0.7rem;\n    }\n  \n    #main-footer {\n      padding-top: 1rem;\n    }\n}\n  \n/*# sourceMappingURL=data:application/json;base64, */"