module.exports = "\n#nav-btn{\n \n  background-color: white;\n  height: 43px;\n  width: 43px;\n  margin-left: 5px;\n}\n\n.nav-item{\n  color: white;\n}\n\n.nav-link{\n  color: white;\n  font-size: 12px;\n  font-family: 'Roboto Mono', monospace;\n  margin-left: 10px;\n}\n\n.navbar-brand{\n  color: white;\n  font-size: 40px;\n  font-family: 'Major Mono Display', monospace;\n  margin-left: 10px;\n}\n\n.container {\n    display: inline-block;\n    cursor: pointer;\n    background-color: black;\n    background-image: url('https://i.imgur.com/FTWl7D3.jpg');\n    background-size: cover; \n    \n  \n  }\n\n#navbar{\n  \n  width: 100%;\n}\n\n.bar1, .bar2, .bar3 {\n    width: 26px;\n    height: 3px;\n    background-color: black;\n    margin-top: 5px ;\n   margin-bottom:5px;\n   margin-right: 7px;\n  \n    /* transition: 0.4s; */\n  }\n\n/* Rotate first bar */\n\n.change .bar1 {\n    -webkit-transform: rotate(-45deg) translate(-9px, 6px) ;\n    transform: rotate(-45deg) translate(-9px, 6px) ;\n  }\n\n/* Fade out the second bar */\n\n.change .bar2 {\n    opacity: 0;\n  }\n\n/* Rotate last bar */\n\n.change .bar3 {\n    -webkit-transform: rotate(45deg) translate(-8px, -8px) ;\n    transform: rotate(45deg) translate(-8px, -8px) ;\n  }\n\n@media (max-width: 768px) {\n    \n  .nav-link{\n      color: white;\n      font-size: 20px;\n      font-family: 'Roboto Mono', monospace;\n     padding-top: 10px;\n     \n      text-align: center\n  }\n  .container{\n    background-color: black;\n    background-image: none;\n    \n  }\n  #navbar{\n    height: 320px;\n  }\n  \n} \n/*# sourceMappingURL=data:application/json;base64, */"